.Info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 16px 0px;
}
.InfoTotal{
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--main-font-color);
    font-size: 12px;
}
.InfoTotal > p{
    font-weight: bold;
}
.deleteAllButton{
    background-color: unset;
    border: none;
    padding: unset;
    color: var(--danger-font-color);
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
}